import formatPrice from "./mapping-func/format-price";
import formatost from "./mapping-func/format-cost";
import getValue from "./mapping-func/get-value";
import imageLink from "./mapping-func/image-link";
import getUnits from "./mapping-func/get-units";
import getCounts from "./mapping-func/get-counts";
import getBarcode from "./mapping-func/get-barcode";

const transformArray = (data, profit, company, round) => {
  const newArray = [];

  data.forEach(item => {
    if (item.measure_prices) {
      const newObj = {
        company: company,                                                                                  // .
        code: item.code,                                                                                   // 
        image: imageLink(item.images),                                                                     //   
        name: item.name.replace(/\s+/g, ' '),                                                              // 
        origin: getValue(item.origin_properties, " ", ""),                              //     
        multiplicity: item.multiplicity,                                                                   //  
        units: getUnits(item.units_counts, item.multiplicity),                                             //   
        counts: getCounts(item.units_counts, item.multiplicity),                                           //     
        price: formatPrice(item, profit, round),                                                           //  
        cost: formatost(item, profit, round),                                                             //    
        barcode: getBarcode(item),                                                                         //-   
      };
      newArray.push(newObj);
    } else {
      console.error(`        ${item.code}`);
    }
  });
  
  return newArray;
};

export default transformArray;